//设置达量断网
export function setOffNet(data){
  return fetch({
    url:'/recharge/manager/setOffNet.do',
    method:'post',
    data
  })
}
//单独断网
export function singleCutNet(data){
  return fetch({
    url:'/recharge/manager/singleCutNet.do',
    method:'post',
    data
  })
}
//断网后恢复上网
export function recoverNet(data){
  return fetch({
    url:'/recharge/manager/recoverNet.do',
    method:'post',
    data
  })
}



