<template>
	<p>
		<el-col :span="12" v-if="isAdmin">
			厂商角色:
			<el-select v-model="value"
					   @change="change"
					   filterable
					   placeholder="请选择"
						style="z-index: 2;"
					   popper-class="select-container">
				<el-option  v-for="item in options" :key="item.mid?item.mid:item.ctId" :label="item.mname?item.mname:item.ctName" :value="item.mid?item.mid:item.ctId">
				</el-option>
			</el-select>
		</el-col>
		<el-col :span="12">
			<el-autocomplete class="inline-input" v-model="input" :fetch-suggestions="querySearch" placeholder="请输入ICCID进行查询"></el-autocomplete>
			<el-button type="primary" @click="btnClick">查询</el-button>
		</el-col>
	</p>
</template>
<script>
	import {
		getListIotCardByMatch
	} from '../../api/admin/usual';
	export default {
		name: 'barPercent',
		props: ['options','isAdmin'],
		data() {
			return {
				input: '',
				value: '',
				iccidArr: []
			};
		},
		watch: {
			options() {
				this.value = this.options[0].mid?this.options[0].mid:this.options[0].ctId;
			}
		},
		mounted() {

		},
		methods: {
			querySearch(queryString, cb) {
				getListIotCardByMatch({
						matchValue: queryString
					})
					.then(res => {
						if(res.msg == 0) {
							this.iccidArr = [];
							for(var i in res.iccidList) {
								var obj = {};
								obj.value = res.iccidList[i];
								this.iccidArr.push(obj);
							}
							cb(this.iccidArr);
						}
					});
			},
			createFilter(queryString) {
				return(v) => {
					return v.value.indexOf(queryString.toLowerCase()) === 0
				};
			},
			change(val) {
				this.$emit('change', val);
			},
			btnClick() {
				this.$emit('btnClick', this.input);
			}
		}
	}
</script>

<style scoped>
	.select-container {
		z-index: 10000;
	}
</style>


