<template>
	<div class="app-container">
		<el-alert title="注意:处理过程约耗时0~5分钟，请稍后查询" show-icon type="warning">
		</el-alert>
		<inputs-header :options="options"  :isAdmin="true" @change='changeCompany' @btnClick="checkIccid" class="clearfix"></inputs-header>
		<el-table :data="tableData" border style="width: 100%" v-loading="isLoad" >
			<el-table-column align="center" prop="number" label="接入号码" width="200">
			</el-table-column>
			<el-table-column align="center" prop="iccid" label="ICCID" width="200">
			</el-table-column>
			<el-table-column align="center" prop="CUMULATION_ALREADY" label="已用量(MB)" width="100" :formatter="formatterAlreadyFlow">
			</el-table-column>
			<el-table-column align="center" prop="CUMULATION_LEFT" label="剩余量(MB)" width="100" :formatter="formatterLeftFlow">
			</el-table-column>
			<el-table-column align="center" prop="CUMULATION_TOTAL" label="总流量(MB)" width="100" :formatter="formatterTotalFlow">
			</el-table-column>
			<el-table-column align="center" prop="isSingleCutNet" label="单独断网">
			</el-table-column>
			<el-table-column align="center" prop="isSetOffNet" label="达量设置" :formatter="formatterSetOffNet">
			</el-table-column>
			<el-table-column align="center" prop="offNetType" label="达量类型" :formatter="formatteroffNetType">
			</el-table-column>
			<el-table-column align="center" prop="offNetValue" label="达量值(MB)" :formatter="formatteroffNetValue" width="100">
			</el-table-column>
			<el-table-column align="center" prop="isAlreadyOffNet" label="是否达量" :formatter="formatterisAlreadyOffNet">
			</el-table-column>
			<el-table-column align="center" label="操作" width="200" fixed="right">
				<template scope="scope">
					<el-button type="button" size="small" v-if="scope.row.operation" :disabled="showSetBtn(scope)" @click="showSetDialog(scope)">设置</el-button>
					<el-button type="button" size="small" v-if="scope.row.operation" :disabled="!scope.row.isSetOffNet" @click="cancelAction(scope)">取消</el-button>
					<el-button type="button" size="small" v-if="scope.row.operation" :disabled="showRecovelBtn(scope)" @click="recovelAction(scope)">恢复</el-button>
					<el-button type="button" size="small" v-if="!scope.row.operation" disabled>申请处理中</el-button>
				</template>
			</el-table-column>
		</el-table>
		<div style="text-align:center;margin: 20px auto;">
			<el-pagination style="display: inline-block" :page-size="10" @current-change="pageChange" :current-page.sync="currentPage" layout="prev, pager, next, jumper" :total="tableCount">
			</el-pagination>
		</div>
		<el-dialog title="设置(达量/单独)断网" :visible.sync="setDialogVisible" size="tiny">
			<el-form :model="form" ref="form" :rules="rules">
				<el-form-item label="ICCID" label-width="120px">
					<el-input v-model="form.iccid" auto-complete="off" disabled></el-input>
				</el-form-item>
				<el-form-item label="接入号码" label-width="120px">
					<el-input v-model="form.number" auto-complete="off" disabled></el-input>
				</el-form-item>
				<el-form-item label="断网类型" prop="type" label-width="120px">
					<el-select v-model="form.type" placeholder="请选择断网类型">
						<el-option label="超出套餐外使用量" value="20"></el-option>
						<el-option label="用户总使用量" value="10"></el-option>
						<el-option label="单独进行断网" value="ADD"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="设置值(单位:MB)" prop="value" v-if="form.type!='ADD'" label-width="120px">
					<el-input type="value" v-model.number="form.value"  placeholder="如:1024" ></el-input>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="setDialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="setAction">确 定</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
/* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */
import InputsHeader from '../../../components/InputsHeader/index';
import { backCN } from 'utils/index'
import {
	giveManufacturerAuthority,
	getListManufacturerGeneral
} from 'api/admin/usual';
import {
	getListOffNetInfo,
	getOffNetInfoByIccid,
	setOffNet,
	singleCutNet,
	recoverNet
} from 'api/offNetInfo';
import {
	toDecimal
} from '../../../filters/index';

export default {
	data() {
		return {
			options: [],
			value8: '',
			tableData: [],
			tableCount: 0,
			currentPage: 1,
			iccidInput: '',
			iccidArr: [],
			isLoad: true,
			setDialogVisible: false,
			form: {
				iccid: '',
				number: '',
				type: '',
				value: null,
			},
			rules: {
				type: [{
					required: true, message: '请选择断网类型'
				}],
				value: [{ required: true, message: '请输入设置值'},
				{ type: 'number', message: '设置值必须为数字值' }]
			},
			operationType: 1,//设置达量类型，1:增加,2:修改
			lableVisible: true
		}
	},
	watch: {},
	mounted() {
		this.initHeader()
	},
	methods: {
		initHeader(){
			this.isLoad = true
			getListManufacturerGeneral()
				.then(res => {
					res.msg === 0 ? this.options = res.mList : this.$message.error(backCN(res.code))
					res.msg === 0 ? '' : this.Load = false
				}).catch(err => {
					this.isLoad = false
					this.$message.error(backCN(err.status))
				})
		},
		showSetDialog(scope) {
			let obj = scope.row
			this.form = {
				'iccid': obj.iccid,
				'number': obj.number,
				'type': obj.offNetType || "10",
				'value':Number(obj.offNetValue) || null
			};
			this.operationType = (obj.isSetOffNet ? 2 : 1)//操作类型，新增还是编辑
			this.setDialogVisible = true
		},
		setAction() {
			this.$refs['form'].validate((valid) => {
				if (valid) {
					let iccid = this.form.iccid;
					let number = this.form.number;
					if (Object.is(this.form.type, 'ADD')) {//单独断网设置
						let action = 'ADD'
						this.setDialogVisible = false;
						this.isLoad=true
						singleCutNet({ iccid, number, action }).then(res => {
							if (res.msg === 0) {
								
								this.$message.success('设置成功')
							} else {
								this.$message.error(backCN(res.code))
							}
							this.ListOffNetInfo(1);
						}).catch(err=>{
							this.isLoad=false
							this.ListOffNetInfo(1);
							this.$message.error(backCN(err.status))
						})
					} else {//普通达量设置
						let action = this.operationType
						let type = this.form.type == 10 ? 1 : 2
						let quota = this.form.value
						this.setDialogVisible = false;
						this.isLoad=true
						setOffNet({ iccid, number, action, type, quota }).then(res => {
							if (res.msg === 0) {
								this.$message({
									type: 'success',
									message: '设置成功!'
								});
							} else {
								this.$message.error(backCN(res.code))
							}
							this.ListOffNetInfo(1);
						}).catch(err=>{
							this.isLoad=false
							this.ListOffNetInfo(1);
							this.$message.error(backCN(err.status))
						})
					}
				} else {
					return false;
				}
			});

		},
		cancelAction(scope) {
			this.$confirm('确定取消该卡的达量设置吗?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				this.isLoad = true;
				let iccid = scope.row.iccid;
				let number = scope.row.number;
				let action = 3;
				let type = scope.row.offNetType == '用户总使用量' ? 1 : 2;
				let quota = scope.row.offNetValue
				this.isLoad=true
				setOffNet({ iccid, number, action, type, quota }).then(res => {
					if (res.msg === 0) {
						this.$message({
							type: 'success',
							message: '取消成功!'
						});
					} else {
						this.$message.error(backCN(res.code))
					}
					this.ListOffNetInfo(1);
				}).catch(err=>{
							this.isLoad=false
							this.ListOffNetInfo(1);
							this.$message.error(backCN(err.status))
						});

			}).catch(() => {
				this.$message({
					type: 'info',
					message: '已撤销取消操作'
				});
			});
		},
		recovelAction(scope) {
			this.$confirm('确定要恢复该卡吗?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				this.isLoad = true;
				let iccid = scope.row.iccid;
				let number = scope.row.number;
				this.isLoad=true
				recoverNet({ iccid, number }).then(res => {
					console.log(111)
					if (res.msg === 0) {
						this.$message.success( '恢复成功!');
					} else {
						this.$message.error(backCN(res.code))
					}
					this.ListOffNetInfo(1);
				}).catch(err=>{
							this.isLoad=false
							this.ListOffNetInfo(1);
							this.$message.error(backCN(err.status))
						})
			}).catch(() => {
				this.$message({
					type: 'info',
					message: '已取消恢复'
				});
			});
		},
		showSetBtn(scope) {
			let isAlreadyOffNet = scope.row.isAlreadyOffNet ? scope.row.isAlreadyOffNet : '否'
			return Object.is(scope.row.isSingleCutNet, "是") || Object.is(isAlreadyOffNet, "是")
		},
		showRecovelBtn(scope) {
			let isAlreadyOffNet = scope.row.isAlreadyOffNet ? scope.row.isAlreadyOffNet : '否'
			return Object.is(scope.row.isSingleCutNet, "否") && Object.is(isAlreadyOffNet, "否")
		},
		changeCompany(val) {
			this.isLoad = true;
			giveManufacturerAuthority({
				mid: val
			}).then(res => {
					if (res.msg == 0) {
						this.ListOffNetInfo(1);
						} else {
					this.isLoad = false
					this.$message.error(backCN(res.code));
				}
			}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN(err.status))
			});
		},
		pageChange(val) {
			this.ListOffNetInfo(val);
		},
		checkIccid(iccid) {
			if (iccid) {
				this.OffNetInfoByIccid(iccid);
			} else {
				this.ListIotCardState(1);
			}
		},
		formatteroffNetValue(val) {
			if (val.offNetValue) {
				return val.offNetValue;
			} else {
				return 0;
			}
		},
		formatteroffNetType(val) {
			if (val.offNetType) {
				if (val.offNetType == 10) {
					return "用户总使用量";
				} else if (val.offNetType == 20) {
					return "超出套餐使用量";
				}
			} else {
				return "无";
			}
		},
		formatterisAlreadyOffNet(val) {
			if (val.isAlreadyOffNet) {
				return val.isAlreadyOffNet;
			} else {
				return "否";
			}
		},
		formatterSetOffNet(val) {
			if (val.isSetOffNet) {
				return "是";
			} else {
				return "否";
			}
		},
		formatterTotalFlow(val) {
			return toDecimal(parseInt(val.CUMULATION_TOTAL) / 1024);
		},
		formatterLeftFlow(val) {
			return toDecimal(parseInt(val.CUMULATION_LEFT) / 1024);
		},
		formatterAlreadyFlow(val) {
			return toDecimal(parseInt(val.CUMULATION_ALREADY) / 1024);
		},
		ListOffNetInfo(page) {
			this.isLoad = true
			getListOffNetInfo({
				page
			})
				.then(res => {
					this.isLoad = false;
					if (res.msg == 0) {
						this.tableData = res.offNetList;
						this.tableCount = res.count;
					} else {
						this.tableData = [];
						this.tableCount = 0;
						res.msg != 1 ? this.$message.error(backCN(res.code)) : ''
					}
				}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN(err.status))
			});;
		},
		OffNetInfoByIccid(iccid) {
			this.isLoad=true
			getOffNetInfoByIccid({iccid}).then(res => {
					this.isLoad = false;
					console.log("res",res)
					if (res.msg == 0) {
						res.iccid = iccid;
						this.tableData =[res];
						this.tableCount = 1;
					} else {
						this.tableData = [];
						this.tableCount = 0;
						res.msg != 1 ? this.$message.error(backCN(res.code)) : ''
					}
				}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN(err.status))
			});
		}
	},
	components: {
		InputsHeader
	}
};
</script>

<style scoped>
.app-container {
	background: #f5f5f5;
}
</style>


